.container {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	gap: 10px;
  height: 2340px;
	width: 95%;
  top: 0px;
	flex-wrap: wrap;
}
.obj3d {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-linejoin: round;
}
.ai {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-width: 2px;
	stroke-linejoin: round;
}
.digitalart {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-width: 2px;
	stroke-linejoin: round;
}
.programmer {
	position: sticky;
	top: 5%;
	overflow: visible;
	stroke: #7f7f7f;
	stroke-width: 2px;
	stroke-linejoin: round;
}
.obj3d-container, .ai-container, .digitalart-container, .programmer-container {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	gap: 10px;
  height: 1900px;
  top: 0px;
	flex-wrap: wrap;
}
.obj3d-container-whole, .ai-container-whole .digitalart-container-whole, .programmer-container-whole {
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
  top: 0px;
	flex-wrap: wrap;
}